<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">你真在和 {{receiveAppUserDto.name}} 聊天</block>
		</cu-custom>
		<view class="cu-chat">
			<template v-for="(item,index) in wechatList">
				
			<view class="cu-item self" v-if="item.isSelf">
				<view class="main">
					<view class="content bg-green shadow">
						<text>{{item.message}}</text>
					</view>
				</view>
				<view class="cu-avatar radius"
					 :style="[{ backgroundImage:'url(' + item.senderAppUserDto.imageUrls + ')' }]">
				</view>
				<view class="date">{{item.sendDateTimeString}}</view>
			</view>
		<!-- 	<view class="cu-info round">对方撤回一条消息!</view> -->
			<view class="cu-item" v-if="item.isSelf==false">
				<view class="cu-avatar radius"
					 :style="[{ backgroundImage:'url(' + item.senderAppUserDto.imageUrls + ')' }]">
				</view>
				<view class="main">
					<view class="content shadow">
						<text>{{item.message}}</text>
					</view>
				</view>
				<view class="date">{{item.sendDateTimeString}}</view>
			</view>
			</template>
		</view>

		<view class="cu-bar foot input" :style="[{bottom:InputBottom+'px'}]">
			<view class="action">
				<text class="cuIcon-sound text-grey"></text>
			</view>
			<input @focus="InputFocus" @blur="InputBlur" v-model="commentContent" placeholder="说点什么~~~"
				:adjust-position="false" class="solid-bottom" :focus="false" maxlength="300"
				cursor-spacing="10"></input>

			<button class="cu-btn bg-green shadow" @click="Send" :disabled="commentContent.length==0">发送</button>
		</view>

	</view>
</template>

<script>
	import store from "@/store";
	import {
		mapGetters
	} from "vuex";

	export default {
		data() {
			return {
				commentContent: "",
				InputBottom: 0,
				receiveUserId: 0,
				wechatList: [],
				receiveAppUserDto:{name:""}
			};
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		onLoad(option) {
			this.receiveUserId = option.id;
			this.GetWechatList();

		},
		methods: {
			async GetWechatList() {
				let url = "/wechat/GetWechatDetailByUserId";
				let {
					success,
					data
				} = await this.$http.PostAsync(url, {
					sendUserId: this.userInfo.id,
					receiveUserId: this.receiveUserId
				});
				if (success) {
					this.receiveAppUserDto=data.receiveAppUserDto;
					this.wechatList = data.wechatDtos.items;
					console.log(this.wechatList);
				}
			},
			async Send() {

				let url = "/wechat/CreateOrEditAsync";
				let {
					success,
					data
				} = await this.$http.PostAsync(url, {

					"sendUserId": this.userInfo.id,
					"message": this.commentContent,
					"isRead": false,
					"receiveUserId": this.receiveUserId,
				});
				if (success) {
					this.$comm.ShowToast("消息已送达");
					this.GetWechatList();
					
				}
				this.commentContent = "";

			},
			InputFocus(e) {
				this.InputBottom = e.detail.height
			},
			InputBlur(e) {
				this.InputBottom = 0
			}
		}
	}
</script>

<style>
	page {
		padding-bottom: 100upx;
	}
</style>
